.appContainer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 125px;
}
.appContainerLeft {
    margin-left: 120px;
    margin-right: 200px;
    margin-bottom: 50px;
    margin-top: 25px;
}
.header {
    font-size: 50px;
}
.content {
    margin-left: 40px;
}

.blue {
    color: rgb(76, 129, 228);
}

.red {
    color: rgb(208, 77, 61);
}

.yellow {
    color: rgb(233, 183, 64);
}

.genBtn {
    background-color: rgb(85, 160, 88);
    border: none;
    padding: 10px 30px;
    color: white;
    margin: 0px;
    margin-bottom: 30px;
    cursor: pointer;
    font-size: 20px;
    border-radius: 50px;
}

.searchBarLeft {
    border-top-left-radius: 50px;
    border-bottom-left-radius: 50px;
    border-top: 1px solid lightgrey;
    border-bottom: 1px solid lightgrey;
    border-left: 1px solid lightgrey;
    border-right: none;
    height: 40px;
    width: 200px;
    padding-left: 10px;
}

.searchBarRight {
    border-top-right-radius: 50px;
    border-bottom-right-radius: 50px;
    border-left: none;
    border-top: 1px solid lightgrey;
    border-bottom: 1px solid lightgrey;
    height: 40px;
    border-right: 1px solid lightgrey;
    width: 200px;

}

.searchBarMiddle {
    border-left: none;
    border-right: none;
    height: 40px;
    border-top: 1px solid lightgrey;
    border-bottom: 1px solid lightgrey;
    width: 200px;
}

.inputContainer {
    margin-top: 5px;
    margin-bottom: 5px;
}
input:focus {
    outline: none;
}
.imageInput {
    border-radius: 50px;
    border-radius: 50px;
    border-left: none;
    border: 1px solid lightgrey;
    height: 40px;
    width: 400px;
    margin-bottom: 10px;
    padding-left: 5px;
}
hr {
    border: 1px solid lightgray;
}
.seoLinks {
    color: black;
    font-weight: bold;
    text-decoration: none;
}

.contentHeaderContainer {
    background-image: url(https://images.pexels.com/photos/2244746/pexels-photo-2244746.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=750&w=1260);
    background-size: cover;
    height: 400px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.contentHeader {
    color: white;
    font-size: 50px;
    margin: 0;
}

