.dropdownContainer {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    margin-top: 20px;
    margin-bottom: 20px;
    width: 300px;
    height: 50px;
}
.dropdown {
    position: relative;
    display: inline-block;
    background-color: rgb(247, 248, 249);
    padding: 12px 40px;
    border-radius: 4px;
    cursor: pointer;
    margin-right: 5px;
    width: 1000px;
}
.dropdown-content {
    display: none;
    position: absolute;
    background-color: #f9f9f9;
    min-width: 160px;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    padding: 12px 16px;
    z-index: 1;
    border-radius: 4px;
}
.dropdown:hover {
    border: 1px solid lightgray;
}
.dropdown:hover .dropdown-content {
display: block;
}
.dropdownLeft {
    right: 0;
}
.dropdownRight {
    left: 0;
}
.contentHeaderButtonContainer {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 500px;
}
.contentHeaderButtons {
    color: #007FCB;
    background-color: white;
    border: 3px solid #007FCB;
    border-radius: 16px;
    font-size: 15px;
    cursor: pointer;
    font-weight:bold;
    padding:10px 40px;
    min-width:25%;
    text-decoration:none;
    /* margin-right: 5px; */
}
.contentHeaderParagraph {
    color: white;
}